import '@babel/polyfill';

import { StylesProvider, ThemeProvider } from '@material-ui/styles';
import App, { Container } from 'next/app';
import React from 'react';

import Offline from 'components/Offline';
import getMuiContext from 'mui/getMuiContext';

class MyApp extends App {
  static async getInitialProps({ Component, ctx }) {
    let pageProps = {};

    if (Component.getInitialProps) {
      pageProps = await Component.getInitialProps(ctx);
    }

    return { pageProps };
  }

  state = {
    offline: false,
  };

  constructor(props) {
    super(props);
    // Material UI SSR strategy: https://github.com/heysailor/material-ui-nextjs
    this.muiContext = getMuiContext();
  }

  componentDidMount() {
    const jssStyles = document.querySelector(`#jss-server-side`);

    if (jssStyles && jssStyles.parentNode) {
      // Remove the server-side injected CSS
      jssStyles.parentNode.removeChild(jssStyles);
    }

    // webfontloader throws an error when imported server-side due to usage
    // of `window`: https://github.com/typekit/webfontloader/issues/383
    // eslint-disable-next-line global-require
    const WebFont = require(`webfontloader`);

    // Load fonts asynchronously so they are not render-blocking. The goal is to
    // deliver the content as quickly as possible.
    WebFont.load({
      google: {
        families: [`Roboto:300,400,500,700?display=swap`],
      },
    });

    // Register service worker, if the browser supports it
    if (`serviceWorker` in window.navigator) {
      window.navigator.serviceWorker.register(`/serviceWorker.js`).catch(() => {
        // Service worker registration failed
      });
    }

    const handleNetworkChange = () => {
      if (!window.navigator.onLine) {
        this.setState({ offline: true });
      } else {
        this.setState({ offline: false });
      }
    };

    handleNetworkChange();
    window.addEventListener(`online`, handleNetworkChange);
    window.addEventListener(`offline`, handleNetworkChange);
  }

  render() {
    const { Component, pageProps } = this.props;
    const { offline } = this.state;

    return (
      <Container>
        <ThemeProvider theme={this.muiContext.theme}>
          <StylesProvider>
            <Component muiContext={this.muiContext} {...pageProps} />
            {offline && <Offline />}
          </StylesProvider>
        </ThemeProvider>
      </Container>
    );
  }
}

export default MyApp;
